'use client';

import { useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const HeroSection: React.FC = () => {
  useEffect(() => {
    const styleSheet = document.createElement('style');
    styleSheet.textContent = `
      .animate-scroll {
        display: inline-block;
        white-space: nowrap;
      }
      .highlight-grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        gap: 20px;
        padding: 20px;
      }
      .highlight-card {
        position: relative;
        overflow: hidden;
        border-radius: 8px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
      }
      .highlight-card img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.3s ease;
      }
      .highlight-card:hover img {
        transform: scale(1.05);
      }
      .highlight-text {
        position: absolute;
        bottom: 10px;
        left: 10px;
        color: white;
        z-index: 1;
      }
    `;
    document.head.appendChild(styleSheet);

    return () => {
      document.head.removeChild(styleSheet);
    };
  }, []);

  const categories = [
    { name: 'Tees', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/1650930e_e762.jpg', link: '/tees' },
    { name: 'Pants', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/0f25034f_c3b5.jpg', link: '/pants' },
    { name: 'Denim', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/1996bc0b_7622.jpg', link: '/denim' },
    { name: 'Shirts', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/74db686e_a247.jpg', link: '/shirts' },
    { name: 'Shorts', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/f9db9668_72d9.jpg', link: '/shorts' },
    { name: 'Bags', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/53a91526_89ca.jpg', link: '/bags' },
  ];

  const featuredSections = [
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/debc76e3_e4aa', text: 'The Vacation Shop', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/aeb3b3e0_5470.png', text: 'The Linen Shop', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/m-2025-jun-live06-17-hp-img3.jpg?wid=1000', text: 'New Shorts', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/m-2025-jun-live06-17-hp-img4.jpg?wid=1000', text: 'Lightwash Denim', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
  ];

  const sustainabilitySection = {
    image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/032b2cc9_0069.jpg',
    text: "Sustainability Looks Better With Receipts. Here's Ours.",
    button: 'Read Our Impact Report',
    link: 'https://www.everlane.com/collections/mens-vacation-shop',
  };

  return (
    <div className="min-h-screen bg-stone-50 text-stone-900 font-serif">
      {/* Hero Video Section */}
      <section className="relative w-full h-[60vh] overflow-hidden">
        <video
          className="absolute top-0 left-0 w-full h-full object-cover"
          src="/images/main.mp4"
          autoPlay
          loop
          muted
          playsInline
        />
        <div className="absolute inset-0 flex items-center justify-center bg-teal-900/40">
          <div className="text-center text-white p-4">
            <h1 className="text-3xl md:text-4xl font-extralight mb-2">Discover Minimalistic Clothing</h1>
            <p className="text-md md:text-lg">Timeless style, sustainable fashion.</p>
          </div>
        </div>
      </section>

      {/* Shop by Category Section with Framer Motion */}
      <section className="py-16 bg-white overflow-hidden">
        <div className="max-w-6xl mx-auto px-4">
          <h2 className="text-xl md:text-2xl font-medium text-center mb-8">Shop by Category</h2>
          <div className="overflow-hidden">
            <motion.div
              className="animate-scroll"
              animate={{ x: '-33.333%' }}
              transition={{ duration: 50, ease: 'linear', repeat: Infinity }}
            >
              <AnimatePresence>
                {[...categories, ...categories, ...categories].map((category, index) => (
                  <motion.a
                    key={index}
                    href={category.link}
                    className="inline-block p-2 w-96"
                    initial={{ opacity: 1 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 1 }}
                    transition={{ duration: 0 }}
                  >
                    <div className="relative h-120 rounded-lg shadow-md overflow-hidden">
                      <img
                        src={category.image}
                        alt={category.name}
                        className="w-full h-full object-cover"
                      />
                      <span className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-black text-xl font-semibold uppercase bg-white bg-opacity-70 p-4 rounded-lg">
                        Shop {category.name}
                      </span>
                    </div>
                  </motion.a>
                ))}
              </AnimatePresence>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Featured Highlights with Pinterest Layout */}
      <section className="py-16 bg-stone-100">
        <div className="max-w-6xl mx-auto px-4">
          <h2 className="text-xl md:text-2xl font-medium text-center mb-8">Highlight Picks</h2>
          <div className="highlight-grid">
            {featuredSections.slice(0, 4).map((section, index) => (
              <a
                key={index}
                href={section.link}
                className="highlight-card"
                style={{
                  height: index % 2 === 0 ? '400px' : '300px',
                }}
              >
                <img
                  src={section.image}
                  alt={section.text || `Featured Section ${index + 1}`}
                  className="highlight-card"
                />
                {section.text && (
                  <div className="highlight-text">
                    <h3 className="text-md md:text-lg font-medium uppercase">{section.text}</h3>
                    <span className="text-xs md:text-sm uppercase">{section.button}</span>
                  </div>
                )}
              </a>
            ))}
          </div>
        </div>
      </section>

      {/* Sustainability Section (Full Width) */}
      <section className="py-16 bg-white">
        <a
          href={sustainabilitySection.link}
          className="relative block w-full"
        >
          <img
            src={sustainabilitySection.image}
            alt="Sustainability"
            className="w-full h-80 md:h-120 object-cover"
          />
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="text-center text-white p-4">
              <h3 className="text-lg md:text-xl font-light">{sustainabilitySection.text}</h3>
              <span className="text-xs md:text-sm uppercase mt-1 inline-block">{sustainabilitySection.button}</span>
            </div>
          </div>
        </a>
      </section>
    </div>
  );
};

export default HeroSection;